import { Component } from '@angular/core';
import { IonicPage, ModalController, ViewController, ToastController } from 'ionic-angular';
import { AngularFirestore } from '@angular/fire/firestore';
import { Collections } from '../../app/app.models';

@IonicPage()
@Component({
  selector: 'page-pay-template-add',
  templateUrl: 'pay-template-add.html',
})
export class PayTemplateAddPage {

  public template: any = {};

  constructor(
    public modal: ModalController,
    public view: ViewController,
    private afs: AngularFirestore,
    private toast: ToastController,
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PayTemplateAddPage');
  }

  async addTemplate() {

    const id = this.template.id;
    await this.afs.collection(Collections.PayTemplate).doc(id).set({
      ...this.template,
      id,
    });

    this.toast.create({ message: 'Costo creado correctamente', duration: 4000 }).present();
    this.view.dismiss();

  }

}



